
import "../styles/GenericMainSection.scss";
import React from "react";
import PhotoCard from "./PhotoCard";
import PriceCard from "./PriceCard";
import { useSelector } from 'react-redux';
import { pacTextContent, pacTitleTextContent, pacPriceCardLabels, initPacPriceCardLabels, pricingSectionTitle } from "./TextData";
import { tandemJumpIcon, tandemVideoIcon, pacPrice, initPacPrice } from "./Constants";
import Zoom from 'react-reveal/Zoom';
import Bounce from 'react-reveal/Bounce';

const PacContent = () => {

    //getting the current language value from the store
    const storeLanguage = state => state.language;
    //Setting up hook on storeLanguage
    const currentLanguage = useSelector(storeLanguage);

    //retrieving the right index for each text value array based on current Store Language
    const pacTitleTextContentIndex = pacTitleTextContent.findIndex( content => content.language === currentLanguage[0].value);
    const pacTextContentIndex = pacTextContent.findIndex( content => content.language === currentLanguage[0].value);
    const pacPriceCardLabelsIndex = pacPriceCardLabels.findIndex( content => content.language === currentLanguage[0].value);
    const initPacPriceCardLabelsIndex = initPacPriceCardLabels.findIndex( content => content.language === currentLanguage[0].value);
    const pricingSectionTitleIndex = pricingSectionTitle.findIndex( content => content.language === currentLanguage[0].value);
    
    return <div id = "pacContent" className = "generic-main-content">
        <div className = "generic-main-content__title">{pacTitleTextContent[pacTitleTextContentIndex].title}</div>
        <PhotoCard className = "photo-card" src = "images/pac/6.png" textContent = {pacTextContent[pacTextContentIndex].text1}/>
        <PhotoCard className = "photo-card" src = "images/pac/7.png" textContent = {pacTextContent[pacTextContentIndex].text2}/>
        <PhotoCard className = "photo-card" src = "images/pac/9.png" textContent = {pacTextContent[pacTextContentIndex].text3}/>
        {/*<Zoom><div className = "generic-main-content__title generic-main-content__title--pricing">{pricingSectionTitle[pricingSectionTitleIndex].title}</div></Zoom>
        <PriceCard icon = {tandemJumpIcon} productName = {pacPriceCardLabels[pacPriceCardLabelsIndex].title} price = {pacPrice}/>
<PriceCard icon = {tandemVideoIcon} productName = {initPacPriceCardLabels[initPacPriceCardLabelsIndex].title} price = {initPacPrice}/>*/}
    </div>
}

export default PacContent;

