
import "../styles/GenericMainSection.scss";
import React from "react";
import PhotoCard from "./PhotoCard";
import PriceCard from "./PriceCard";
import SectionDetails from "./SectionDetails";
import { useSelector } from 'react-redux';
import { tandemTextContent, tandemSectionElements, tandemTitleTextContent, tandemJumpPriceCardLabels, tandemVideoPriceCardLabels, pricingSectionTitle, faqTextContent } from "./TextData";
import { tandemJumpIcon, tandemVideoIcon, tandemPrice, tandemVideoPrice } from "./Constants";
import Zoom from 'react-reveal/Zoom';
import Faq from "./Faq";


const TandemContent = () => {

    //getting the current language value from the store
    const storeLanguage = state => state.language;
    //Setting up hook on storeLanguage
    const currentLanguage = useSelector(storeLanguage);

    //retrieving the right index for each text value array based on current Store Language
    const tandemTitleTextContentIndex = tandemTitleTextContent.findIndex( content => content.language === currentLanguage[0].value);
    const tandemTextContentIndex = tandemTextContent.findIndex( content => content.language === currentLanguage[0].value);
    const tandemSectionElementsIndex = tandemSectionElements.findIndex( content => content.language === currentLanguage[0].value);
    const tandemJumpPriceCardLabelsIndex = tandemJumpPriceCardLabels.findIndex( content => content.language === currentLanguage[0].value);
    const tandemVideoPriceCardLabelsIndex = tandemVideoPriceCardLabels.findIndex( content => content.language === currentLanguage[0].value);
    const pricingSectionTitleIndex = pricingSectionTitle.findIndex( content => content.language === currentLanguage[0].value);
    const faqTextContentIndex = faqTextContent.findIndex( content => content.language === currentLanguage[0].value);
    
    return <div id = "tandemContent" className = "generic-main-content">
        <div className = "generic-main-content__title">{tandemTitleTextContent[tandemTitleTextContentIndex].title}</div>
        <PhotoCard className = "photo-card" src = "images/tandem/9.png" textContent = {tandemTextContent[tandemTextContentIndex].text1} direction = "left"/>
        <PhotoCard className = "photo-card" src = "images/tandem/4.png" textContent = {tandemTextContent[tandemTextContentIndex].text2} direction = "left"/>
        <PhotoCard className = "photo-card" src = "images/tandem/5.png" textContent = {tandemTextContent[tandemTextContentIndex].text3} direction = "left"/>
        <SectionDetails elements = {tandemSectionElements[tandemSectionElementsIndex].items}/>
        <Zoom><div className = "generic-main-content__title generic-main-content__title--pricing">{pricingSectionTitle[pricingSectionTitleIndex].title}</div></Zoom>
        <PriceCard icon = {tandemJumpIcon} productName = {tandemJumpPriceCardLabels[tandemJumpPriceCardLabelsIndex].title} price = {tandemPrice}/>
        <PriceCard icon = {tandemVideoIcon} productName = {tandemVideoPriceCardLabels[tandemVideoPriceCardLabelsIndex].title} price = {tandemVideoPrice}/>
        <div className = "generic-main-content__title">F.A.Q</div>
        <Faq elements = {faqTextContent[faqTextContentIndex].questions}/>
    </div>
}

export default TandemContent;
