import "../styles/SectionDetails.scss";
import React from "react";

const SectionDetails = (props) => {

    const reactElementsArray = props.elements.map((data) => {
        return (
            <div className = "section-details__element">
            <i className={data.icon}></i>
            <p>{data.text}</p>
        </div>
        );
    });

        return <div className = "section-details">{reactElementsArray}</div>
}

export default SectionDetails;
