import "../styles/PriceCard.scss";
import React from "react";

const PriceCard = (props) => {

        return <div className = "price-card">
                    <div className = "price-card__icon"><i className = {props.icon}></i></div>
                    <div className = "price-card__product-name">{props.productName}</div>
                    <div className = "price-card__price">{props.price}</div>
        </div>
}

export default PriceCard;
