import "../styles/SectionDetails.scss";
import React from "react";
import Paragraphs from "./Paragraphs";
import "../styles/faq.scss";
//To use the global state for EXPAND/MINIMIZE purpose
import store from "./store";
import { useSelector, useDispatch } from "react-redux";
import Fade from 'react-reveal/Bounce';

const Faq = (props) => {

    /*const dispatch = useDispatch();

    //getting the current opened faq element from the store
        const storeCurrentFaqElement = state => state.currentFaqElement;
    //Setting up hook on storeLanguage
        const currentFaqElement = useSelector(storeCurrentFaqElement);*/

    const showOrHide = (elementId) => {
        if (document.getElementById(elementId).className == "faq__element__content--visible") {
            document.getElementById(elementId).setAttribute("class", "faq__element__content")
        }
        else {
            document.getElementById(elementId).setAttribute("class", "faq__element__content--visible")
        }
    }

    const reactElementsArray = props.elements.map((data) => {
        const currentContent = "faqContent"+data.id

        
        return (
            <div id = "test" className = "faq__element">
                <div className = "faq__element__title-container">
                    <i className="fas fa-chevron-down" onClick  = { () => {
                        showOrHide(currentContent);
                    }}></i>

                    <p className ="faq__element__title">{data.title}</p>
                </div>
                <Fade left><div id = {currentContent} className = "faq__element__content">
                    <Paragraphs textContent = {data.paragraphs}/>
                </div></Fade>
        </div>
        );
    });

        return <div className = "faq">{reactElementsArray}</div>
}

export default Faq;
