import "../styles/WhoWeAre.scss";
import React from "react";
import Paragraphs from "./Paragraphs";
import Bounce from 'react-reveal/Bounce';

const WhoWeAre = (props) => {

    const reactElementsArray = props.textContent.map((data) => {
        return (
        <p>{data}</p>
        );
    });

    console.log(props.textContent)
        return <div className = {props.className}>
                    
                    
                    <div className = "who-we-are__title">{props.title}</div>
                    
                    <div className = "who-we-are__images-container">
                    <Bounce left>
                    <img className = "who-we-are__image" src = {props.src[0]}></img>
                    </Bounce>
                    <Bounce left>
                    <img className = "who-we-are__image" src = {props.src[1]}></img>
                    </Bounce>
                    <Bounce left>
                    <img className = "who-we-are__image" src = {props.src[2]}></img>
                    </Bounce>
                    </div>

                    <Bounce right>
                    <div className = "who-we-are__text">
                        <Paragraphs textContent = {props.textContent}/>
                        
                    </div>
                    </Bounce>
                

    </div>
}

export default WhoWeAre;
