/*import '../App.css';*/
import BackgroundCarousel from './BackgroundCarousel'
import TitleCard from './TitleCard';
import HomeTypeWriter from './HomeTypeWriter';
import Banner from './Banner';
import TandemContent from './TandemContent';
import PacContent from './PacContent';
import Footer from './Footer';
import WhoWeAre from './WhoWeAre';
import { whoWeAreTextContent } from './TextData';
import "../styles/Base.scss";
import '../styles/TitleCard.scss';
import PhotoCard from './PhotoCard';
import store from "./store";
import { useSelector } from 'react-redux';
import { tandemTitleCardTextContent, pacTitleCardTextContent, contactFormLabels, contactErrorMessages, locationTextContent } from './TextData';
import LanguageSelector from './LanguageSelector';
import ContactForm from './ContactForm';
import Location from './Location';
import Fluent from './Fluent';
  


function App() {

  //getting the current language value from the store
const storeLanguage = state => state.language;
//Setting up hook on storeLanguage
const currentLanguage = useSelector(storeLanguage);

    //Finding out the index of whoWeAreTextContent for current store Language
    const whoWeAreTextContentIndex = whoWeAreTextContent.findIndex( content => content.language === currentLanguage[0].value);

    //Finding out the index of tandemTitleCardTextContent for current store Language
    const tandemTitleCardTextContentIndex = tandemTitleCardTextContent.findIndex( content => content.language === currentLanguage[0].value);

    //Finding out the index of tandemTitleCardTextContent for current store Language
    const pacTitleCardTextContentIndex = pacTitleCardTextContent.findIndex( content => content.language === currentLanguage[0].value);
    
    //Finding out the index of ContactFormLabels for current store Language
    const ContactFormLabelsIndex = contactFormLabels.findIndex( content => content.language === currentLanguage[0].value);

    //Finding out the index of ContactFormLabels for current store Language
    const ContactErrorMessagesIndex = contactErrorMessages.findIndex( content => content.language === currentLanguage[0].value);

    //Finding out the index of locationTextContent for current store Language
    const locationTextContentIndex = locationTextContent.findIndex( content => content.language === currentLanguage[0].value);
    

  return <fragment>
            <LanguageSelector/>
            <Banner/>
            <div className = "carousel">
              <BackgroundCarousel numberOfImages = "9" imageFolder = "home" cycle = "5000" className = "background-carousel background-carousel--full-width" />
            </div>

            {/*<div className = "home-typewriter">
              <HomeTypeWriter/>
            </div>*/}

            <Fluent/>

            <div>
              <TitleCard anchor = "#tandemContent" title = {tandemTitleCardTextContent[tandemTitleCardTextContentIndex].title} catchPhrase = {tandemTitleCardTextContent[tandemTitleCardTextContentIndex].catchPhrase} className = "title-card title-card--enter-left"/>
            </div>

            <div>
              <TitleCard anchor = "#pacContent" title = {pacTitleCardTextContent[pacTitleCardTextContentIndex].title} catchPhrase = {pacTitleCardTextContent[pacTitleCardTextContentIndex].catchPhrase} className = "title-card title-card--enter-right"/>
            </div>

            <TandemContent/>
            <Location src = "images/location.png" className = "location" textContent = {locationTextContent[locationTextContentIndex].text} direction = "left"/>
            <PacContent/>
            <div>
              <WhoWeAre className = "who-we-are" src = {["images/me2.jpg", "images/me.jpg", "images/me3.jpg"]} title = {whoWeAreTextContent[whoWeAreTextContentIndex].title} textContent = {whoWeAreTextContent[whoWeAreTextContentIndex].texts} direction = "left"/>
            </div>

            <ContactForm  formTitle = {contactFormLabels[ContactFormLabelsIndex].formTitle}
                          nameLabel = {contactFormLabels[ContactFormLabelsIndex].name} 
                          phoneLabel ={contactFormLabels[ContactFormLabelsIndex].phone} 
                          emailLabel ={contactFormLabels[ContactFormLabelsIndex].email} 
                          productLabel={contactFormLabels[ContactFormLabelsIndex].about} 
                          messageLabel ={contactFormLabels[ContactFormLabelsIndex].message} 
                          pacLabel = {contactFormLabels[ContactFormLabelsIndex].pacLabel} 
                          tandemLabel = {contactFormLabels[ContactFormLabelsIndex].tandemLabel} 
                          nameError = {contactErrorMessages[ContactErrorMessagesIndex].name}
                          emailError = {contactErrorMessages[ContactErrorMessagesIndex].email}
                          phoneError = {contactErrorMessages[ContactErrorMessagesIndex].phone}
                          messageError = {contactErrorMessages[ContactErrorMessagesIndex].message}
                          />

            <Footer/>
            
        </fragment>

}

export default App
