import logo from '../assets/logo.svg';
import "../styles/BackgroundCarousel.scss";
import React, { useEffect, useState } from "react";
import { nanoid } from "nanoid";




const BackgroundCarousel = (props) => {


    //Defining the number of images to cycle
    const numberOfImages = props.numberOfImages;
    //Defining image folder name inside public folder
    const imageFolder = props.imageFolder;
    //Defining the cycle time
    const cycle = props.cycle;
    //Creating the hook we'll be using to cycle through images
    const [currentPhotoIndex, cyclePhotoIndex] = React.useState(1);
    let nextPhotoIndex;

    //defining the value of nextPhotoIndex
    if (currentPhotoIndex != numberOfImages) {
      nextPhotoIndex = currentPhotoIndex + 1;
    } else {
      nextPhotoIndex = 1;
    }
    //Defining the function to update the variable defined in the hook
    const nextPicture = () => {

      //deciding wether to increment or reset currentPhotoIndex to 0
      console.log(currentPhotoIndex+' sur '+numberOfImages);
      if (currentPhotoIndex == numberOfImages) {
        cyclePhotoIndex(1);
      }
      else {
        cyclePhotoIndex(currentPhotoIndex + 1);
      } 
    }

    //useEffect will be executed everytime the component renders
    useEffect(() => {
      //Setting an interval of 2000ms before getting next picture
      const interval = setInterval(() => {
        //executing the function to update the hook variable.
        nextPicture();
      }, cycle);
      //This I really don't understand but it is necessary.
      return () => clearInterval(interval);
      //This is necessary so that useeffect can access the value of currentPhotoIndex
    }, [currentPhotoIndex]);

    //returning a DIV containing the current picture.
    return <div key={nanoid()} className = {props.className}>
        <img src={'images/'+imageFolder+'/'+currentPhotoIndex+'.png'}  alt={imageFolder} className='background-carousel__image background-carousel__image--current' />
        <img src={'images/'+imageFolder+'/'+nextPhotoIndex+'.png'}  alt={imageFolder} className='background-carousel__image background-carousel__image--next' />
    </div>
    
}

export default BackgroundCarousel
