import "../styles/Paragraphs.scss";
import React from "react";

const Paragraphs = (props) => {

    const reactElementsArray = props.textContent.map((data) => {
        return (
        <p>{data}</p>
        );
    });
        
    return(
        <div>{reactElementsArray}</div>  
    );
      


}

export default Paragraphs;
