import logo from '../assets/logo.jpg'
import "../styles/Banner.scss"
import LanguageSelector from './LanguageSelector';
import store from "./store";
import { useSelector } from 'react-redux';
import { bannerTitle } from './TextData';



function Banner() {

  
  //getting the current language value from the store
  const storeLanguage = state => state.language;
  //Setting up hook on storeLanguage
  const currentLanguage = useSelector(storeLanguage);

    //Finding out the index for current store Language
    const titleIndex = bannerTitle.findIndex( title => title.language === currentLanguage[0].value);    
    
    return <div className = "banner" >
            <img src={logo} alt='Parachutisme' className='banner__logo' />
            <h1 className='banner__title'>{bannerTitle[titleIndex].title}</h1>
    </div>
    
  }
export default Banner
