import logo from '../assets/logo.svg';
import "../styles/BackgroundCarousel.scss";
import React from "react";
import { useSelector } from 'react-redux';



const TitleCard = (props) => {

    const title = props.title;
    const catchPhrase = props.catchPhrase;
   
    return <div className = {props.className}>
        <a href = {props.anchor} className = "title-card__title ">{props.title}</a>
        <div className = "title-card__catchphrase">{props.catchPhrase}</div>
    </div>
    
}

export default TitleCard;
