import "../styles/LanguageSelector.scss"
import { useSelector, useDispatch } from "react-redux"
import React, { useEffect, useState } from "react";
import store from "./store";

function LanguageSelector() {

    const dispatch = useDispatch();
    const storeLanguage = state => state.language;

    const currentLanguage = useSelector(storeLanguage);


    return  <div className = "language-selector__icons">
                <img src="images/FR.png" alt='Parachutisme' className='' onClick = { () => {
                    dispatch({ type: 'language/ChangeValue', payload: 'FR' })

                    console.log(store.getState()
                    )}}/>
                
                <img src="images/EN.png" alt="" className='' onClick = { () => {
                    dispatch({ type: 'language/ChangeValue', payload: 'EN' })

                    console.log(store.getState()
                    )}}/>

                
            </div>
  }
export default LanguageSelector
