import "../styles/Location.scss";
import React from "react";
import Paragraphs from "./Paragraphs";
import Bounce from 'react-reveal/Bounce';
import Fade from 'react-reveal/Fade';
import { locationIcon } from "./Constants";

const Location = (props) => {

    const reactElementsArray = props.textContent.map((data) => {
        return (
        <p>{data}</p>
        );
    });

    console.log(props.textContent)
        return <div className = {props.className}>
                    <i className = {locationIcon}></i>
                    <Fade>
                    <img className = "location_image" src = {props.src}></img>
                    </Fade>
                
                    <Bounce up>
                    <div className = "location__text">
                        <Paragraphs textContent = {props.textContent}/>
                        
                    </div>
                    </Bounce>
                

    </div>
}

export default Location;
